.video-fact-control {
  width: 100%;
  height: 86%;
  background: white;
  display: flex;
  padding: 5px;
  padding-top: 3px;
  overflow: hidden;
  border-top: 2px solid #F0F0F2;
}
.video-fact-control .video-slice {
  height: 100%;
  width: 100%;
  min-width: 160px;
  overflow-x: auto;
  overflow-y: hidden;
  display: flex;
  align-items: center;
}
.video-fact-control .video-slice div:nth-child(1) {
  margin: 10;
}
.video-fact-control .video-slice .video-slice-card {
  display: flex;
  flex-shrink: 0;
  width: 180px;
  height: 172px;
  background: #FFFFFF;
  margin-left: 5px;
  border-radius: 6px;
}
.video-fact-control .video-slice .video-slice-card .transition-button {
  display: flex;
  align-items: center;
  margin-left: 5px;
  cursor: pointer;
  opacity: 0;
}
.video-fact-control .video-slice .video-slice-card .transition-button .transition-icon {
  width: 14px;
  height: 24px;
  background: url(data:image/png;base64,);
  background-size: 14px 24px;
}
.video-fact-control .video-slice .video-slice-card .transition-button .cannot-transition-icon {
  width: 14px;
  height: 24px;
  background: url(/static/media/icon-cannot-generate.736d239f.png);
  background-size: 14px 24px;
}
.video-fact-control .video-slice .video-slice-card .transition-button:hover {
  display: flex;
  align-items: center;
  margin-left: 5px;
  cursor: pointer;
  opacity: 1;
}
.video-fact-control .video-slice .video-slice-card .transition-button:hover .transition-icon {
  width: 14px;
  height: 24px;
  background: url(data:image/png;base64,);
  background-size: 14px 24px;
}
.video-fact-control .video-slice .video-slice-card .transition-button:hover .cannot-transition-icon {
  width: 14px;
  height: 24px;
  background: url(/static/media/icon-cannot-generate.736d239f.png);
  background-size: 14px 24px;
}
.video-fact-control .video-slice .video-slice-card .complete-card {
  width: 100%;
  height: 100%;
  border-radius: 6px;
  padding: 6px 5px 0;
}
.video-fact-control .video-slice .video-slice-card .complete-card .closeIcon {
  color: #484f5d;
  padding: 6;
  position: absolute;
  z-index: 2;
  top: 10px;
  right: 10px;
  display: inline;
  -webkit-transform: scale(1.4);
  transform: scale(1.4);
}
.video-fact-control .video-slice .video-slice-card .complete-card .above-card {
  position: -webkit-sticky;
  position: sticky;
  z-index: 999;
  background-color: white;
  width: 100%;
  height: 15px;
  display: flex;
  justify-content: flex-end;
}
.video-fact-control .video-slice .video-slice-card .complete-card .above-card .fact-description {
  width: 80%;
  height: 100%;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  /*超出部分隐藏*/
  font-size: 12.5px;
  padding-left: 4px;
  cursor: default;
  top: 2px;
  left: 5px;
  width: 115px;
  font-weight: 500;
}
.video-fact-control .video-slice .video-slice-card .complete-card .above-card .delete-card-btn {
  width: 10%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: default;
}
.video-fact-control .video-slice .video-slice-card .complete-card .above-card .delete-card-btn :hover {
  color: #FCBE31;
}
.video-fact-control .video-slice .video-slice-card .complete-card .below-card {
  position: -webkit-sticky;
  position: sticky;
  z-index: 1;
  width: 100%;
  height: 62%;
  margin-bottom: 5px;
  background-color: #fff;
  overflow: hidden;
  margin-top: -10px;
}
.video-fact-control .video-slice .video-slice-card .complete-card .below-card div[id^="vischart"] {
  width: 100% !important;
  height: 100% !important;
}
.video-fact-control .video-slice .video-slice-card .complete-card .below-card div[id^="vischart"] text {
  font-size: 6px !important;
}
.video-fact-control .video-slice .video-slice-card .complete-card .below-card div[id^="vischart"] #unitsvg {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}
.video-fact-control .video-slice .video-slice-card .complete-card .below-card > div > svg {
  zoom: 0.8;
}
.video-fact-control .video-slice .video-slice-card .complete-card .footer-card {
  width: 100%;
  margin-top: 2%;
}
.video-fact-control .video-slice .video-slice-card .complete-card .footer-card .footer-num {
  width: 18px;
  height: 18px;
  border-radius: 0 3px 0 0;
  font-size: 8px;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
}
.video-fact-control .video-slice .video-slice-card .complete-card .footer-text {
  height: 43px;
  width: 97%;
  padding-left: 5px;
  padding-left: 5px 2px;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
  overflow: hidden;
  font-family: "NotoSansHans-Regular", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 10px;
  color: #000;
  text-align: justify;
  line-height: 14px;
}
.video-fact-control .center-divider {
  width: 1.5%;
  background-color: #FFFFFF;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 8px;
  margin-left: -15px;
}
.video-fact-control .center-divider .divider-line {
  width: 1px;
  margin: 0 auto;
  height: 125px;
  border: 1px solid #F0F0F2;
  background-color: #F0F0F2 100%;
}
.video-fact-control .add-empty-slice {
  background-color: #FFFFFF;
  height: 100%;
  display: flex;
  align-items: center;
}
.video-fact-control .add-empty-slice .add-empty-slice-btn {
  height: 125px;
  width: 140px;
  background-color: #F0F0F2;
  border: 2px solid #F0F0F2;
  border-radius: 6.8px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.video-fact-control .add-empty-slice .add-empty-slice-btn .add-logo-icon {
  font-size: 40px;
  color: #7C91A6;
}
